
p{
  font-family: 'Josefin Slab', serif;

}

.iosBlurBg {
  resize: horizontal;
  position: relative;
  /*width: 100%;
	height: 100vh;*/
  border-top: 450px solid transparent;
  border-left: 550px solid rgba(0, 0, 0, 0.04);
  border-bottom: 450px solid transparent;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.1)), to(rgba(255, 255, 255, 0.1)));
  background: -webkit-linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1));
  background: -o-linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1));
  background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1));
  /*box-shadow: 1px 1px 25px #999;*/
}

.whiteBg {
  resize: horizontal;
  position: absolute;
  top: -410px;
  right: 0;
  border-top: 410px solid transparent;
  border-left: 505px solid rgba(255, 255, 255, 1);
  border-bottom: 410px solid transparent;
  border-right: 30px solid transparent;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.1)), to(rgba(255, 255, 255, 1)));
  background: -webkit-linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 1));
  background: -o-linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 1));
  background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 1));
}

.bottomEnter {
  position: absolute;
  top: 500px;
  left: 150px;
  z-index: -2;
  border-top: 200px solid transparent;
  border-right: 300px solid #15224a;
  border-bottom: 150px solid transparent;
  -webkit-transform: rotate(-2deg);
      -ms-transform: rotate(-2deg);
          transform: rotate(-2deg);
}

.bottomBlurBg {
  position: absolute;
  top: 490px;
  left: 110px;
  z-index: -1;
  border-top: 200px solid transparent;
  border-right: 260px solid rgba(69, 138, 182, 0.4);
  border-bottom: 120px solid transparent;
  -webkit-transform: rotate(10deg);
      -ms-transform: rotate(10deg);
          transform: rotate(10deg);
}

