.text-white {
  color: #fff;
}

.enterText {
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 300;
  font-family: 'Josefin Slab', serif;
}

.loginForm {
  position: absolute;
  top: 300px;
  left: 70px;
}

.title {
  font-weight: 300;
  font-size: 24px;
  line-height: 1;
}

.title span {
  font-weight: 800;
  /*letter-spacing: 3px;*/
}

.title hr {
  width: 150px;
  border-width: 2px;
  border-color: #67e2fb;
