}

/*= input focus effects css
=========================== */

:focus {
  outline: none;
}

.col-3 {
  /* float: left; */
  width: 100%;
  margin-top: 20px;
  margin-bottom: 0;

}

input {
  margin-top: 15px;
  font: 15px/24px 'Josefin Slab', serif;
  color: #333;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  letter-spacing: 1px;
  width: 200px;
}

input::-webkit-input-placeholder {
  text-transform: uppercase;
  letter-spacing: 0;
  font-size: 20px;
  font-weight: 300;
  padding-left: 15px;
}

input:-ms-input-placeholder {
  text-transform: uppercase;
  letter-spacing: 0;
  font-size: 20px;
  font-weight: 300;
  padding-left: 15px;
}

input::-ms-input-placeholder {
  text-transform: uppercase;
  letter-spacing: 0;
  font-size: 20px;
  font-weight: 300;
  padding-left: 15px;
}

input::placeholder {
  text-transform: uppercase;
  letter-spacing: 0;
  font-size: 20px;
  font-weight: 300;
  padding-left: 15px;
}

.effect-2 {
  border: 0;
  padding: 7px 0;
  border-bottom: 1px solid #ccc;
}

.forget button {
  margin-top: 50px;
  border-radius: 0;
  color: #aaa;
  margin-left: 50px;
  font-weight: 700;
  font-family: 'Josefin Slab', serif;

}



